body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.Page {
  margin-top: 80px;
}



.home {
}

    .homt-top {
        width: 100%;
    }

        .home-photo-section {
            padding: 20px;

            box-sizing: border-box;
        }

            .home-photo {
                box-sizing: border-box;
                padding: 1%;
                box-shadow: 0 3px 12px 3px rgba(0,0,0,0.2);
                border-radius: 20px;
                -webkit-transition: -webkit-transform ease-in-out 0.1s;
                transition: -webkit-transform ease-in-out 0.1s;
                transition: transform ease-in-out 0.1s;
                transition: transform ease-in-out 0.1s, -webkit-transform ease-in-out 0.1s;
            }

            .home-photo:hover {
                cursor: pointer;
                /* transform: scale(1.05); */
            }
                .home-photo-img {
                    width: 100%;
                    border-radius: 20px;
                }

        .home-noteboard-section {
            box-sizing: border-box;
            padding: 30px;
        }

            .noteboard {
                height: 100%;
                padding: 10px;
                border-radius: 20px;
                box-shadow: 0 6px 12px 2px rgba(0,0,0,0.2);
                display: flex;
                flex-direction: column;
                -webkit-transition: -webkit-transform ease-in-out 0.2s;
                transition: -webkit-transform ease-in-out 0.2s;
                transition: transform ease-in-out 0.2s;
                transition: transform ease-in-out 0.2s, -webkit-transform ease-in-out 0.2s;
                -webkit-transform: translate(0,2%) scale(1.035);
                        transform: translate(0,2%) scale(1.035);

            }

            .noteboard:hover {
                cursor: pointer;
                -webkit-transform: translate(0,2%) scale(1.07);
                        transform: translate(0,2%) scale(1.07);
            }
                .noteboard-title {
                    margin-left: 10px;
                    margin-top: 5px;
                    margin-bottom: 5px;
                    text-align: left;
                    font-size: 1.5vw;
                }

                .noteboard-tutorial {
                    font-size: 17px;
                    display: block;
                    box-sizing: border-box;
                    padding: 10px;
                    color: #bbbbbb;
                    margin-top: 50px;
                }


                .noteboard-notes {
                    width: 100%;
                    flex: 1 1;
                    overflow-y: scroll;
                    padding: 10px;

                }
                    .noteboard-text {
                        color: rgb(29, 76, 119);
                        width: 100%;
                        overflow: visible;
                        margin-top: 10px;
                        box-sizing: border-box;
                        resize: none;
                        border: none;
                        padding: 10px;
                        border-radius: 10px;
                        box-shadow: 0 0px 0px 0px rgba(0,0,0,0.2);
                        border: 1px solid rgb(168, 206, 241);
                        outline: 0;
                        font-size: 15px;
                        -webkit-transition: border ease-in-out 0.2s, box-shadow ease-in-out 0.1s;
                        transition: border ease-in-out 0.2s, box-shadow ease-in-out 0.1s;

                    }
                    .noteboard-passage {
                        color: black;
                        width: 100%;
                        overflow: visible;
                        box-sizing: border-box;
                        margin-top: 10px;
                        resize: none;
                        border: none;
                        border: 1px solid white;
                        padding: 10px;
                        border-radius: 10px;
                        box-shadow: 0 3px 12px 0px rgba(0,0,0,0.2);
                        outline: 0;
                        font-size: 15px;
                        -webkit-transition: border ease-in-out 0.2s, box-shadow ease-in-out 0.1s;
                        transition: border ease-in-out 0.2s, box-shadow ease-in-out 0.1s;
                    }

                .noteboard-button {
                    width: 100%;
                    overflow-x: hidden;
                    white-space: nowrap;
                }
                    .noteboard-remove {
                        border: 0;
                        box-sizing: border-box;
                        width: 40%;
                        height: 40px;
                        margin: 20px 20px 0 0;
                        background-color: rgb(255, 60, 60);
                        color: white;
                        border-radius: 25px;
                        display: inline-block;
                        -webkit-transition: width ease-in-out 0.1s;
                        transition: width ease-in-out 0.1s;
                    }
                    .noteboard-submit {
                        border: 0;
                        width: 70%;
                        height: 40px;
                        margin: 10px 15% 10px 15%;
                        background-color: rgb(65, 199, 70);
                        color: white;
                        border-radius: 25px;
                        display: inline-block;
                        -webkit-transition: width ease-in-out 0.1s;
                        transition: width ease-in-out 0.1s;
                    }

    .cards {
        width: 100%;
        padding: 20px;
        box-sizing: border-box;
        list-style: none;
    }

        .cards-card {
            width: 30%;
            height: 40vw;
            margin-left: 2.5%;
            margin-bottom: 10%;
            margin-top: 0;
            float: left;
        }

            .card {
                -webkit-transition: -webkit-transform ease-in-out 0.2s;
                transition: -webkit-transform ease-in-out 0.2s;
                transition: transform ease-in-out 0.2s;
                transition: transform ease-in-out 0.2s, -webkit-transform ease-in-out 0.2s;
                -webkit-transform: scale(1);
                        transform: scale(1);
                box-shadow: 0 3px 12px 0px rgba(0,0,0,0.2);
                border-radius: 20px;
                height: 100%;
                margin: 0;
                box-sizing: border-box;
                padding: 40px;
            }

            .card:hover {
                -webkit-transform: scale(1.03);
                        transform: scale(1.03);
            }

                .card-link {
                    width: 100%;
                    height: 100%;
                    display: block;
                }

                .card-link:hover {
                    cursor: pointer;
                    text-decoration: none;
                }
                    .card-img {
                        margin-top: 40%;
                        width: 40%;
                    }

/*** smart phone ****/
@media screen and (max-width: 1000px) {

    .home-photo-section {
        width: 100%;
    }

    .home-noteboard-section {
        width: 100%;
        height: 120vw;
    }

        .noteboard-title {
            font-size: 4vw;
        }

        .noteboard-text {
            font-size: 18px;
        }

        .noteboard-passage {
            font-size: 18px;
        }

        .noteboard-submit {
            height: 50px;
        }

    .cards-card {
        width: 100%;
        height: 120vw;
        margin-left: 0;
        margin-top: 10%;
    }

}

/*** large desktop ****/
@media screen and (min-width: 1000px) {

    .home-photo-section {
        width: 70%;
        float: left;
    }

    .home-noteboard-section {
        height: 40vw;
        width: 30%;
        float: left;
    }
}
/* global */
