  margin-top: 30px;
  margin-bottom: 20px !important;
}

/* settings */
.settings-bar {
  width: 500px;
  width: var(--login-bar-width);
	margin: 150px 325px;
	margin: 150px var(--login-bar-margin);
}

* {
  -webkit-touch-callout: none; /* iOS Safari */
    -webkit-user-select: none; /* Safari */ /* Konqueror HTML */
       -moz-user-select: none; /* Firefox */
        -ms-user-select: none; /* Internet Explorer/Edge */
            user-select: none; /* Non-prefixed version, currently
                                  supported by Chrome and Opera */
}

.swipe {
    position: fixed;
    padding: 100px 20px;
    height: 100%;
    box-sizing: border-box;
}

    .swipe-card {
        width: 50vh;
        height: 80vh;
        min-height: 480px;
        min-width: 300px;
        border-radius: 20px;
        overflow: hidden;
        position: fixed;
        left: 50%;

        box-shadow: 0 3px 12px 3px rgba(0,0,0,0.2);
        background-color: white;



    }

    .swipe-card:hover {
        cursor: pointer;
    }

        .swipe-card-dislike {
            width: 55%;
            right: 0;
            position: absolute;
        }

        .swipe-card-like {
            width: 55%;

            position: absolute;
        }

        .swipe-card-image {
            height: 60vh;

        }

        .swipe-card-title {
            text-align: center;
        }

            .swipe-card-info {
                width: 6%;
                -webkit-transform: translate(0,-5%);
                        transform: translate(0,-5%);
                -webkit-transition: -webkit-transform ease-in-out 0.1s;
                transition: -webkit-transform ease-in-out 0.1s;
                transition: transform ease-in-out 0.1s;
                transition: transform ease-in-out 0.1s, -webkit-transform ease-in-out 0.1s;
                margin: 10px auto;

            }

            .swipe-card-info:hover {
                -webkit-transform: translate(0,-5%) scale(1.1);
                        transform: translate(0,-5%) scale(1.1);
            }

        .swipe-card-bot {
            padding: 0 20px;
            box-sizing: border-box;
        }


            .swipe-people {
            }
            .swipe-type {
                margin: 0;
            }

            img {
                -webkit-user-select: none;
                -moz-user-select: none;
                -o-user-select: none;
                -ms-user-select: none;
                    user-select: none;
                }

                    .fade-enter {
                        opacity: 0.01;
                    }
                    .fade-enter-active {
                        opacity: 1;
                        -webkit-transition: opacity 500ms ease-in;
                        transition: opacity 500ms ease-in;
                    }
                    .fade-exit {
                        opacity: 1;
                    }
                    .fade-exit-active {
                        opacity: 0.01;
                        -webkit-transition: opacity 500ms ease-in;
                        transition: opacity 500ms ease-in;
                    }


.memory {
  margin-top: 15%;
}

.year {
  width: 200px;
  height: 80px;
  font-size: 50px;
  background-color: #FF9F55;
  color: white;
  margin-left: 44%;
  margin-top: -8%;
  z-index: 2;
  text-align: center;
  position: relative;
}

.button {
  background-color: rgb(255, 255, 255, 0.3);
}

/* The actual timeline (the vertical ruler) */
.timeline::after {
    content: '';
    position: absolute;
    width: 6px;
    height: 200%;
    background-color: grey;
    top: 18%;
    left: 50%;
}

.photos {
  width: 30%;
  margin-left: 24%;
  margin-bottom: 8%;
  margin-top: 8%;
}

/*Circle on the timeline*/
.photos::after {
  content: '';
  position: absolute;
  width: 25px;
  height: 25px;
  background-color: white;
  border: 4px solid #FF9F55;
  border-radius: 50%;
  z-index: 1;
  left: 49.4%;
}

/*For images on the left of timeline*/
.left {
  padding-left: 20px;
}

.arrow-left {
  margin-right: 50px;
  font-size: 40px;
  color: grey;
}

.time-left {
  position: absolute;
  margin-left: 53%;
  font-size: 30px;
}

/*For images on the right of timeline*/
.right {
  margin-left: 55%;
}

.arrow-right {
  font-size: 40px;
  color: grey;
  margin-right: 50px;
  margin-left: 40px;
}

.time-right {
  position: absolute;
  margin-left: 43%;
  font-size: 30px;
}



.popup {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin: auto;
  background-color: rgba(0,0,0, 0.5);
  z-index: 3;
}
.popup_inner {
  position: absolute;
  left: 25%;
  right: 25%;
  top: 25%;
  bottom: 25%;
  margin: auto;
  background: white;
}

.carousel {
  width: 100%;
  position: relative;
}

.prev {
  position: absolute;
  float: left;
  font-size: 40px;
  margin-top: -35%;
}

.next {
  position: absolute;
  float: right;
  font-size: 40px;
  margin-top: -35%;
}

